/* CUSTOM FONTS */

@font-face {
	font-family: 'Mont';
	src: url('../fonts/Mont-Regular.eot');
	src: local('☺'),
		url('../fonts/Mont-Regular.woff') format('woff'),
		url('../fonts/Mont-Regular.ttf') format('truetype'),
		url('../fonts/Mont-Regular.svg') format('svg');
	font-weight: 400;
	font-style: normal;
}


@font-face {
	font-family: 'Mont';
	src: url('../fonts/Mont-Bold.eot');
	src: local('☺'),
		url('../fonts/Mont-Bold.woff') format('woff'),
		url('../fonts/Mont-Bold.ttf') format('truetype'),
		url('../fonts/Mont-Bold.svg') format('svg');
	font-weight: 600;
	font-style: normal;
}


@font-face {
	font-family: 'Mont';
	src: url('../fonts/Mont-Heavy.eot');
	src: local('☺'),
		url('../fonts/Mont-Heavy.woff') format('woff'),
		url('../fonts/Mont-Heavy.ttf') format('truetype'),
		url('../fonts/Mont-Heavy.svg') format('svg');
	font-weight: 800;
	font-style: normal;
}




@mixin font-render {
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-moz-osx-font-smoothing: grayscale;
}

@mixin transition-cubic {
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	transition: all 0.3s ease;
	transition-duration: 500ms;
	-webkit-transition-duration: 500ms;
	transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
	-webkit-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1)
}

@mixin transition-bg {
	-webkit-transition: all .4s ease-in-out;
	-moz-transition: all .4s ease-in-out;
	-ms-transition: all .4s ease-in-out;
	-o-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;
}

@mixin transition {
	-webkit-transition: all .25s ease-in-out;
	-moz-transition: all .25s ease-in-out;
	-ms-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
}


$color-dark : #282828;


/* BODY */
* {
	outline: none !important;
}

body {
	margin: 0;
	padding-left: 150px;
	padding-right: 50px;
	font-family: 'Mont';
	font-size: 17px;
	color: $color-dark;
	@include transition-bg;
	@include font-render;
}


/* LINKS */
a {
	color: $color-dark;
	@include transition;
}

a:hover {
	text-decoration: underline;
	color: $color-dark;
}

a:active {
	text-decoration: underline;
}

/* HTML ELEMENTS */
img {
	max-width: 100%;
}

p {
	line-height: 1.5;
}


/* FORM ELEMENTS */
input[type="text"] {
	width: 100%;
	height: 54px;
	border: 1px solid #eee;
	padding: 0 20px;
}

textarea {
	width: 100%;
	height: 160px;
	border: 1px solid #eee;
	padding: 15px 20px;
}

button[type="submit"] {
	height: 58px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-ms-flex-align: center;
	-ms-flex-pack: justify;
	justify-content: space-between;
	text-align: center;
	border: none;
	margin: 0;
	padding: 0;

	* {
		@include transition;
	}

	strong {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
		height: 58px;
		border: 1px solid $color-dark;
		font-size: 13px;
		font-weight: 600;
		text-transform: uppercase;
		padding: 0 30px;
		background: $color-dark;
		color: #fff;

		&:hover {
			text-decoration: none;
		}
	}

	b {
		width: 20px;
		height: 1px;
		background: #fff;
		display: inline-block;
		margin-left: 20px;
		margin-top: 8px;
	}

	i {
		width: 0;
		height: 0;
		display: inline-block;
		margin-left: 0;
		margin-right: auto;
		border-style: solid;
		border-width: 4px 0 4px 4px;
		border-color: transparent transparent transparent #fff;
		opacity: 1;
		margin-top: 5px;
	}

	&:hover b {
		width: 50px;
		margin-left: 20px;
	}

	&:hover i {
		opacity: 0;
	}
}



/* CUSTOM CONTAINER */
@media (min-width: 1240px) {
	.container {
		max-width: 1200px;
	}
}


/* CUSTOM ELEMENTS */
.color-dark {
	background-color: $color-dark;

	.left-side ul {
		width: 100vh;
		line-height: 1;
		display: block;
		margin: 0;
		margin-top: 50vh;
		margin-left: -9px;
		padding: 0;
		transform: rotate(-90deg);
		transform-origin: 0 0;
		position: absolute;
		left: 50%;
		top: 50%;
		text-align: center;

		li {
			display: inline-block;
			margin: 0 10px;
			padding: 0;
			list-style: none;

			a {
				display: block;
				font-size: 13px;
				font-weight: 600;
				position: relative;
				padding: 0 2px;
				color: #fff;

				moe &:hover {
					text-decoration: none;
				}

				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					bottom: 6px;
					height: 1px;
					width: 0;
					transition: width 0s ease, background .25s ease;
				}

				&:after {
					content: '';
					display: block;
					position: absolute;
					right: 0;
					bottom: 6px;
					height: 1px;
					width: 0;
					background: #fff;
					transition: width .25s ease;
				}

				&:hover:before {
					width: 100%;
					background: #fff;
					transition: width .25s ease;
				}

				&:hover:after {
					width: 100%;
					background: transparent;
					transition: all 0s ease;
				}
			}
		}
	}

	.left-side .logo img {
		-webkit-filter: invert(100%);
		filter: invert(100%);
	}

	.left-side .gotop img {
		-webkit-filter: invert(100%);
		filter: invert(100%);
	}
}

.color-light {
	background-color: #fff;
}

.color-undefined {
	background-color: transparent;
}

.overflow {
	overflow: hidden;
}



/* ODOMETER */
.odometer {
	line-height: 1;

	.odometer-digit {
		padding: 0;
	}

	.odometer-digit-inner {
		left: 0;
	}
}


/* PAGINATION */
.pagination {
	width: 100%;
	display: flex;
	flex-wrap: wrap;

	li.page-item {
		margin-right: 5px;

		&:first-child a.page-link {
			padding-left: 0;
		}

		&.active a.page-link {
			border: 1px solid $color-dark;
			background: none;
		}

		a.page-link {
			border: none;
			color: $color-dark;
			font-size: 13px;
			font-weight: 600;
			border-radius: 50%;

			&:hover {
				background: none;
				opacity: 0.5;
			}

			&:focus {
				border: none;
				background: none;
				box-shadow: none;
			}
		}
	}
}



/* TRANSITION OVERLAY */
.transition-overlay {
	width: 100vw;
	height: 100vh;
	position: fixed;
	right: calc(-100% - 200px);
	bottom: 0;
	background: $color-dark;
	z-index: 99;
	@include transition-cubic;
	visibility: hidden;

	&:before {
		content: "";
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 200px 100vh 0;
		border-color: transparent $color-dark transparent transparent;
		position: absolute;
		left: -200px;
		top: 0;
	}

	&.active {
		right: 0;
		visibility: visible;
	}
}




/* PRELOADER */
.preloader {
	position: fixed;
	top: 0;
	left: 0;
	margin: auto;
	width: 100vw;
	height: 100vh;
	background: $color-dark;
	opacity: 1;
	@include transition-cubic;
	z-index: 99;
	transition-delay: 0.65s;

	* {
		@include transition-cubic;
	}

	&:before {
		content: "";
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 200px 100vh 0;
		border-color: transparent $color-dark transparent transparent;
		position: absolute;
		left: -200px;
		top: 0;
	}

	&:after {
		content: "";
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 100vh 0 0 200px;
		border-color: transparent transparent transparent $color-dark;
		position: absolute;
		right: -200px;
		top: 0;
	}

	.inner {
		width: 100vw;
		height: 100vh;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		overflow: hidden;

		.logo {
			display: inline-block;
			margin: 0;
			animation: fadeInUp ease 0.6s;

			img {
				height: 50px;
			}
		}

		.percentage {
			width: 100%;
			font-size: 70vh;
			line-height: 1;
			font-weight: 800;
			color: #fff;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			text-align: center;
			opacity: 0.05;
			transition-delay: 0.10s;
		}
	}
}


.page-loaded.preloader {
	left: calc(-100% - 200px);
	visibility: hidden;
}

.page-loaded.preloader .logo {
	transform: translateX(-100px);
	opacity: 0;
}

.page-loaded.preloader .percentage {
	margin-left: 100px;
	opacity: 0;
}




/* BURGER */
.burger {
	margin-top: 1px;
	z-index: 3;
	display: inline-block;
	width: 50px;
	height: 50px;
	padding: 0;
	border: none;
