			margin: 0 10px;
			padding: 0;
			list-style: none;

			a {
				display: block;
				font-size: 13px;
				font-weight: 600;
				position: relative;
				padding: 0 2px;
				text-transform: uppercase;


				&:hover {
					text-decoration: none;
				}

				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					bottom: 6px;
					height: 1px;
					width: 0;
					transition: width 0s ease, background .25s ease;
				}

				&:after {
					content: '';
					display: block;
					position: absolute;
					right: 0;
					bottom: 6px;
					height: 1px;
					width: 0;
					background: $color-dark;
					transition: width .25s ease;
				}

				&:hover:before {
					width: 100%;
					background: $color-dark;
					transition: width .25s ease;
				}

				&:hover:after {
					width: 100%;
					background: transparent;
					transition: all 0s ease;
				}
			}
		}
	}

	.gotop {
		width: 50px;
		padding: 14px;
		position: absolute;
		left: 50px;
		bottom: 35px;
		display: none;

		img {
			display: block;
		}
	}
}


/* SLIDER */
.slider {
	width: calc(100% + 50px);
	height: 560px;
	display: flex;
	flex-wrap: wrap;
	background: $color-dark;
	margin-right: -50px;
	overflow: hidden;
	position: relative;

	&:hover .swiper-container .swiper-slide {
		margin-left: -10px;
	}

	&:hover .swiper-button-next span {
		width: 100%;
	}

	.swiper-container {
		width: 100%;
		height: 100%;
		position: relative;

		.swiper-slide {
			width: calc(100% - 80px);
			display: -webkit-box;
			display: -ms-flexbox;
			display: -webkit-flex;
			display: flex;
			-webkit-box-pack: center;
			-ms-flex-pack: center;
			-webkit-justify-content: center;
			justify-content: center;
			-webkit-box-align: center;
			-ms-flex-align: center;
			-webkit-align-items: center;
			align-items: center;
			opacity: 0.3;
			@include transition;
			overflow: hidden;
			background-size: cover;
			background-position: center;
		}

		.swiper-slide-active {
			opacity: 1;
			z-index: 2;
		}

		.swiper-slide-active .slide-inner {
			opacity: 1;
		}

		.slide-inner {
			width: 100%;
			padding: 0 100px;
			padding-right: 40%;
			opacity: 0;
			@include transition;

			figure {
				display: block;
				margin-bottom: 10px;

				img {
					max-width: 120px;
				}
			}

			h2 {
				font-size: 52px;
				font-weight: 600;
				color: #fff;
				margin-left: -3px;
			}

			.link {
				display: block;
				margin-top: 50px;

				a {
					display: inline-block;
					font-weight: 600;
					color: #fff;
					font-size: 13px;
					position: relative;

					&:before {
						content: "";
						width: 100%;
						height: 6px;
						background: #fff;
						position: absolute;
						left: 0;
						bottom: 0;
						opacity: 0.3;
					}

					&:after {
						content: "";
						width: 0;
						height: 6px;
						background: #fff;
						position: absolute;
						right: 0;
						bottom: 0;
						@include transition;
					}

					&:hover {
						text-decoration: none;
					}

					&:hover:after {
						width: 100%;
					}
				}
			}
		}
	}
}


/* SWIPER BUTTONS */
.swiper-button-next {
	width: 80px;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	position: absolute;
	left: auto;
	right: 20px;
	top: 0;
	z-index: 2;
	background: none;
	text-align: right;

	span {
		width: 50%;
		height: 1px;
		margin-left: auto;
		background: #fff;
		display: inline-block;
		@include transition;
	}

	b {
		font-weight: 600;
		display: block;
		font-size: 12px;
		color: #fff;
		margin-top: 5px;
	}
}



/* SWIPER PAGINATION */
.swiper-pagination-bullet {
	opacity: 1;
	background: rgba(0, 0, 0, 0.2);
	position: relative;
	height: 40px;
	width: 40px;
	background: none;
	text-align: center;
	transform: scaleX(-1) scale(0.7);
	margin: 0;

	&:before {
		content: "";
		width: 100%;
		height: 100%;
		border: 3px solid rgba(255, 255, 255, 0.3);
		border-radius: 50%;
		position: absolute;
		left: 0;
		top: 0;
	}
}

.swiper-pagination-bullet-active {
	color: #fff;
	background: none;

	&:after {
		content: "";
		width: 14px;
		height: 14px;
		border-radius: 50%;
		background: #fff;
		position: absolute;
		left: 50%;
		top: 50%;
		margin-left: -7px;
		margin-top: -7px;
	}
}

.swiper-pagination-bullet svg {
	position: absolute;
	top: 0;
	right: 0;
	width: 40px;
	height: 40px;
	transform: rotateY(-180deg) rotateZ(-90deg);
}

.swiper-pagination-bullet.swiper-pagination-bullet-active svg circle {
	animation-play-state: running;
	stroke-width: 3px;
	animation: countdown 6s linear forwards;
}

.swiper-pagination-bullet svg circle {
	stroke-dasharray: 113px;
	stroke-dashoffset: 0px;
	stroke-linecap: round;
	stroke-width: 0;
	stroke: #fff;
	fill: none;
	animation: none;
	animation-play-state: paused;
	animation-fill-mode: none;
}


