					content: "";
					width: 30%;
					height: 3px;
					background: $color-dark;
					position: absolute;
					left: -25px;
					bottom: 0;
				}
			}

			form {
				display: block;

				input[type="text"] {
					margin-bottom: 10px;
				}

				button[type="submit"] {
					font-weight: 600;
					font-size: 14px;
					color: #fff;
					background: $color-dark;
					padding: 0 30px;
				}
			}

			.categories {
				display: block;
				margin: 0;
				padding: 0;

				li {
					display: block;
					margin: 0;
					padding: 3px 0;
					list-style: none;

					&:before {
						content: "";
						width: 5px;
						height: 5px;
						float: left;
						border-radius: 50%;
						background: $color-dark;
						margin-right: 15px;
						margin-top: 11px;
					}

					span {
						float: right;
						opacity: 0.5;
					}
				}
			}

			.tags {
				display: block;
				margin: 0;
				padding: 0;

				li {
					display: inline-block;
					margin-right: 5px;
					margin-bottom: 10px;
					padding: 0;
					list-style: none;

					a {
						display: inline-block;
						border: 1px solid rgba(40, 40, 40, 0.1);
						line-height: 30px;
						font-size: 14px;
						padding: 0 10px;

						&:hover {
							text-decoration: none;
							border: 1px solid $color-dark;
							background: $color-dark;
							color: #fff;
						}
					}
				}
			}

			.works {
				display: block;
				margin: 0;
				margin-left: -5px;
				margin-right: -5px;
				padding: 0;

				li {
					width: 33.33333%;
					float: left;
					margin: 0;
					padding: 5px;
					list-style: none;
				}
			}
		}
	}
}



/* POST STICKY */
.post-sticky {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	position: relative;

	.post-image {
		width: 100%;
		margin-bottom: 0;

		img {
			width: 100%;
			opacity: 0.5;
		}
	}

	.post-content {
		width: 100%;
		margin: 0;
		position: absolute;
		left: 0;
		bottom: 0;
		color: #fff;
		padding: 50px;
		padding-right: 30%;

		.avatar {
			width: 100%;
			margin-bottom: 20px;
			font-size: 13px;

			img {
				height: 60px;
				margin-right: 20px;
				border-radius: 50%;
			}

			a {
				text-decoration: underline;
				color: #fff;
			}
		}

		.post-title {
			display: block;
			margin-bottom: 15px;
			font-weight: 700;
			font-size: 4vw;

			a {
				display: block;
				color: #fff;
			}
		}

		.post-date {
			display: block;
			margin-bottom: 30px;
		}

		.post-intro {
			display: block;
			margin-bottom: 20px;
			font-size: 18px;
			line-height: 1.7;
		}

		.post-link {
			display: inline-block;
			font-weight: 700;
			padding-bottom: 8px;
			position: relative;
			color: #fff;

			&:hover {
				text-decoration: none;

				&:after {
					width: 0;
				}
			}

			&:before {
				content: "";
				width: 100%;
				height: 3px;
				background: rgba(255, 255, 255, 0.2);
				position: absolute;
				left: 0;
				bottom: 0;
			}

			&:after {
				content: "";
				width: 100%;
				height: 3px;
				background: #fff;
				position: absolute;
				right: 0;
				bottom: 0;
				@include transition;
			}
		}
	}
}




/* WORK WITH US */
.work-with-us {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	padding: 150px 0;
	color: #fff;

	h6 {
		font-size: 17px;
		font-weight: 600;
		margin-bottom: 20px;
	}

	h2 {
		font-size: 6vw;
		font-weight: 800;
		margin-bottom: 50px;
	}

	p {
		display: block;
		margin: 0;
		font-size: 19px;
	}

	a {
		color: #fff;
		text-decoration: underline;
	}

	small {
		display: block;
		margin: 15px 0;
	}
}




/* CONTACT */
.contact {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	padding-top: 150px;

	h5 {
		font-weight: 600;
		margin-bottom: 20px;
		display: block;
	}

	address {
		display: block;
		margin-bottom: 50px;

		p {
			margin: 0;
		}

		a {
			text-decoration: underline;
		}
	}

	.contact-form {
		display: block;
		margin-top: 50px;

		h5 {
			margin-bottom: 40px;
		}

		#contact {
			display: block;

			.form-group {
				display: block;
				position: relative;
				margin-bottom: 35px;

				span {
					width: 100%;
					line-height: 54px;
					position: absolute;
					left: 0;
					top: 0;
					padding: 0 20px;
					z-index: 0;
					@include transition;

					&.label-up {
						font-size: 13px;
						line-height: 16px;
						top: -20px;
						font-weight: 600;
					}
				}

				input[type="text"] {
					background: none;
					position: relative;
					z-index: 2;
				}

				input:focus+span {
					font-size: 13px;
					line-height: 16px;
					top: -20px;
					font-weight: 600;
				}

				textarea {
					background: none;
					position: relative;
					z-index: 2;
				}

				textarea:focus+span {
					font-size: 13px;
					line-height: 16px;
					top: -20px;
					font-weight: 600;
				}

				label.error {
					color: red;
					margin-top: 5px;
					margin-bottom: 0;
					font-weight: 600;
					font-size: 13px;
				}
			}
		}

		#success,
		#error {
			display: none;
			float: left;
		}

		#error {
			background: red;
			color: #fff;
		}

		#success {
			background: green;
			color: #fff;
		}

		.alert {
			border: none;
			border-radius: 0;
			padding: 20px 30px;
		}
	}
}


/* FOOTER */
.footer {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	padding: 150px 0;

	.career {
		display: block;
		border-bottom: 1px solid rgba(40, 40, 40, 0.1);
		padding-bottom: 100px;
		margin-bottom: 100px;

		h6 {
			font-size: 17px;
			font-weight: 600;
			margin-bottom: 20px;
		}

		h2 {
			font-size: 3vw;
			font-weight: 800;
			margin-bottom: 50px;
		}
	}

	.logo {
		display: block;
		margin: 0;

		img {
			height: 42px;
		}
	}

	h5 {
		font-size: 15px;
		font-weight: 600;
		margin-bottom: 20px;
	}

	.sub-footer {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		margin-top: 100px;
		font-size: 13px;

		ul {
			display: inline-block;
			margin-left: 0;
			margin-right: auto;
			margin-bottom: 0;
			padding: 0;

			li {
				display: inline-block;
				margin-right: 20px;
				padding: 0;
				list-style: none;

				a {
					display: block;
					position: relative;
					padding: 0 2px;

					&:hover {
						text-decoration: none;
					}

					&:before {
						content: '';
						display: block;
						position: absolute;
						left: 0;
						bottom: 8px;
						height: 1px;
						width: 0;
						transition: width 0s ease, background .25s ease;
					}

					&:after {
						content: '';
						display: block;
						position: absolute;
						right: 0;
						bottom: 8px;
						height: 1px;
						width: 0;
						background: $color-dark;
						transition: width .25s ease;
					}

					&:hover:before {
						width: 100%;
						background: $color-dark;
						transition: width .25s ease;
					}

					&:hover:after {
						width: 100%;
						background: transparent;
						transition: all 0s ease;
					}
				}
			}
		}

		span {
			margin-right: 0;
		}
	}
}



/* ANIMATIONS */
@keyframes countdown {
	from {
		stroke-dashoffset: 113px;
	}

	to {
		stroke-dashoffset: 0;
	}
}



